@import "style.scss";
@import "assets/variables";

html, body {
	background-color: transparent !important;
}

.sf-header{
	background: {
		image: url('../img/hero-feature1-mobile.jpg');
	};
}

.sf-info{
	background-color: #3d4d65;

	.first{
		background-color: #ffffff;
		.content{
			padding: {
				top: 39px;
				bottom: 37px;
			}
		}
	}

	.second{
		background-color: #3d4d65;

		.content{
			padding: {
				top: 114px;
				bottom: 114px;
			}

			text-align: center;

			.contact-img{
				img{
					width: 368px;
				}
			}
		}
	}
}

.sf-testimonial{
	.wrapper{
		@extend .gradient;
		height: 100%;
		background: -moz-linear-gradient(top,  rgba(255,255,255,0) 0%, rgba(170,176,175,0) 10%, rgba(67,79,77,0.9) 100%); /* FF3.6+ */
		background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(255,255,255,0)), color-stop(10%,rgba(170,176,175,0)), color-stop(100%,rgba(67,79,77,0.9))); /* Chrome,Safari4+ */
		background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(170,176,175,0) 10%,rgba(67,79,77,0.9) 100%); /* Chrome10+,Safari5.1+ */
		background: -o-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(170,176,175,0) 10%,rgba(67,79,77,0.9) 100%); /* Opera 11.10+ */
		background: -ms-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(170,176,175,0) 10%,rgba(67,79,77,0.9) 100%); /* IE10+ */
		background: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(170,176,175,0) 10%,rgba(67,79,77,0.9) 100%); /* W3C */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#e6434f4d',GradientType=0 ); /* IE6-9 */
	}

	p{
		width: 552px;
		color:  rgba(255, 255, 255, 0.95);
		font-size: 27px;
		text-shadow: 3px 5px 6px rgba(0, 0, 0, 0.25);
		text-align: center;
		margin-top: 298px;
	}
}

.sf-article{
	.first{
		background-color: #ffffff;

		.content{
			padding: {
				top: 39px;
				bottom: 37px;
			};
		}
	}

	.second{
		background-color: #d2d2d2;

		.content{
			max-width: 100%;
			height: 434px;
			background-image: url('../img/02.jpg');
			background-position: 81% 10%;
		}
	}
}


.sf-quote{
	@extend .gradient;
	background: -moz-linear-gradient(top,  rgba(255,255,255,0) 0%, rgba(60,182,227,0.1) 100%); /* FF3.6+ */
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(255,255,255,0)), color-stop(100%,rgba(60,182,227,0.1))); /* Chrome,Safari4+ */
	background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(60,182,227,0.1) 100%); /* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(60,182,227,0.1) 100%); /* Opera 11.10+ */
	background: -ms-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(60,182,227,0.1) 100%); /* IE10+ */
	background: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(60,182,227,0.1) 100%); /* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#1a3cb6e3',GradientType=0 ); /* IE6-9 */

	background-color: #2e2e30;
	padding: 39px 0;
	text-align: center;

	p{

		margin: 0 auto;
		color:  #b1bfc2;
		font-size: 32.33px;
	}
}

.sf-trackers{
	@extend .gradient;
	/* IE9 SVG, needs conditional override of 'filter' to 'none' */
	background: url(data:image/svg+xml;base64,);
	background: -moz-linear-gradient(top,  rgba(2,2,2,0.1) 0%, rgba(255,255,255,0) 100%); /* FF3.6+ */
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(2,2,2,0.1)), color-stop(100%,rgba(255,255,255,0))); /* Chrome,Safari4+ */
	background: -webkit-linear-gradient(top,  rgba(2,2,2,0.1) 0%,rgba(255,255,255,0) 100%); /* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(top,  rgba(2,2,2,0.1) 0%,rgba(255,255,255,0) 100%); /* Opera 11.10+ */
	background: -ms-linear-gradient(top,  rgba(2,2,2,0.1) 0%,rgba(255,255,255,0) 100%); /* IE10+ */
	background: linear-gradient(to bottom,  rgba(2,2,2,0.1) 0%,rgba(255,255,255,0) 100%); /* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1a020202', endColorstr='#00ffffff',GradientType=0 ); /* IE6-8 */

	background-color: #ececec;

	padding: 39px 0;
	text-align: center;
	position: relative;
	z-index: 9999;

	p{
		letter-spacing: -0.48px;
		color:  #666c70;
		font:{
			size: 24px;
			family: Montserrat;
		}

		margin-bottom: 40px;
	}

	.col-md-4:last-child p{
		margin-bottom: 0;
	}


	.circle-border{
		margin: {
			top: 0;
			bottom: 23px;
			right: auto;
			left: auto;
		}

		border-radius: 50%;
		background-color:  #ffffff;
		box-shadow: 1px 3px 3px 0px rgba(98, 98, 98, 0.15);
		width: 240px;
		height: 240px;

		vertical-align: middle;

		img{
			margin-top: 68px;
		}
	}

}

.sf-costumers{
	background-color: #ffffff;

	.col-md-6:first-child{
		height: 340px;

		img{
			top: -83px;
			position: absolute;
			z-index: 0;
			right: 27px;
		}
	}

	.col-md-6:last-child{
		padding-top: 29px;
		background-color: #ffffff;
	}

	h3{
		@extend .feature-h3;
	}

	p{
		@extend .feature-p;

		&:first-child{
			margin-bottom: 51px;
		}
	}
}

@media (max-width: $xs-screen) {
	.sf-hero{
		p{
			margin-bottom: 209px;
		}
	}

	.sf-article{
		.image{
			background-position: center;
		}
	}

	.sf-costumers{
		padding-bottom: 40px;
	}
}

@media (min-width: $md-screen) {
	.sf-header{
		background: {
			image: url('../img/hero-feature1.jpg');
		}
	}

	.sf-info{
		.first{
			.content{
				padding: {
					top: 118px;
					bottom: 117px;
					left: 79px;
					right: 51px;
				}

				p{
					width: 432px;
				}
			}
		}

		.second{
			.content{
				padding: {
					left: 123px;
					top: 131px;
					bottom: 64px;
				}

				.contact-img{
					img{
						width: 368px;
					}
				}
			}
		}
	}

	.sf-testimonial{
		.wrapper{
			background: -moz-linear-gradient(top,  rgba(255,255,255,0) 0%, rgba(170,176,175,0) 45%, rgba(67,79,77,0.9) 100%); /* FF3.6+ */
			background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(255,255,255,0)), color-stop(45%,rgba(170,176,175,0)), color-stop(100%,rgba(67,79,77,0.9))); /* Chrome,Safari4+ */
			background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(170,176,175,0) 45%,rgba(67,79,77,0.9) 100%); /* Chrome10+,Safari5.1+ */
			background: -o-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(170,176,175,0) 45%,rgba(67,79,77,0.9) 100%); /* Opera 11.10+ */
			background: -ms-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(170,176,175,0) 45%,rgba(67,79,77,0.9) 100%); /* IE10+ */
			background: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(170,176,175,0) 45%,rgba(67,79,77,0.9) 100%); /* W3C */
		}

		p{
			width: 748px;
			margin: {
				top: 317px;
				left: auto;
				right: auto;
			}

			font-size: 23.46px;
			line-height: 32px;

		}
	}

	.sf-article{
		.first{
			h3{
				width: 385px;
			}

			.content{
				padding: {
					top: 69px;
					bottom: 66px;
					left: 79px;
					right: 78px;
				}

			}
		}
	}

	.sf-quote{
		padding: {
			top:46px;
			bottom:49px;
		}

		p{
			font-size: 21.83px;
			line-height: 18.2px;
			width: 494px;
		}
	}

	.sf-trackers{
		.row{
			margin: 0 auto;
			width: 725px;
		}
		.circle-border{
			width: 140px;
			height: 140px;

			img{
				margin-top: 38px;
				width: 60px;
				height: 60px;
			}
		}

		p{
			font-size: 18px;
			margin: 0;
		}
	}

	.sf-costumers{
		position: relative;

		.second{
			.content{
				padding-top: 60px;
				padding-left: 40px;
			}
		}

		.col-md-6{
			padding: 0;
		}

		img{
			right: 0px;
		}

	}

	.sf-tour{
		position: relative;
		z-index: 99999;
	}
}
